import _ from 'lodash';
import tfp from '@/assets/images/sections/tfp.jpg'
import art from '@/assets/images/sections/art.jpg'
import clips from '@/assets/images/sections/clips.jpg'
import commerce from '@/assets/images/sections/commerce.jpg'
import familyandkids from '@/assets/images/sections/familyandkids.jpg'
import fashion from '@/assets/images/sections/fashion.jpg'
import holidays from '@/assets/images/sections/holidays.jpg'
import interrior from '@/assets/images/sections/interrior.jpg'
import lovestory from '@/assets/images/sections/lovestory.jpg'
import motiondesign from '@/assets/images/sections/motiondesign.jpg'
import objective from '@/assets/images/sections/objective.jpg'
import others from '@/assets/images/sections/others.jpg'
import party from '@/assets/images/sections/party.jpg'
import personal from '@/assets/images/sections/personal.jpg'
import wedding from '@/assets/images/sections/wedding.jpg'

export default {
  PHOTO_FILTERS: {
    'tfp': {
      icon: tfp,
      description: 'TFP',
    },
    'fashion': {
      icon: fashion,
      description: 'FASHION',
    },
    'wedding': {
      icon: wedding,
      description: '',
    },
    'family': {
      icon: familyandkids,
      description: '  ',
    },
    'holidays': {
      icon: holidays,
      description: '',
    },
    'party': {
      icon: party,
      description: '  ',
    },
    'personal': {
      icon: personal,
      description: '',
    },
    'lovestory': {
      icon: lovestory,
      description: 'LOVE STORY',
    },
    'commerce': {
      icon: commerce,
      description: '',
    },
    'interrior': {
      icon: interrior,
      description: '',
    },
    'objective': {
      icon: objective,
      description: '',
    },
    'other': {
      icon: others,
      description: '',
    }
  },
  VIDEO_FILTERS: {
    'clips': {
      icon: clips,
      description: '',
    },
    'art': {
      icon: art,
      description: '',
    },
    'wedding': {
      icon: wedding,
      description: '',
    },
    'holidays': {
      icon: holidays,
      description: '',
    },
    'party': {
      icon: party,
      description: '  ',
    },
    'lovestory': {
      icon: lovestory,
      description: 'LOVE STORY',
    },
    'commerce': {
      icon: commerce,
      description: '',
    },
    'other': {
      icon: others,
      description: '',
    },
    'motiondesign': {
      icon: motiondesign,
      description: 'MOTION DESIGN',
    }
  },
  get PHOTO_ARRAY(){
    return _.values(_.mapValues(this.PHOTO_FILTERS, function(value, key) { 
      value.category = key;
      return value; 
    }));
  },
  get VIDEO_ARRAY(){
    return _.values(_.mapValues(this.VIDEO_FILTERS, function(value, key) { 
      value.category = key;
      return value; 
    }));
  }
}



